import uniLoadMore from '@/components/uni-load-more.vue';
import server from '../../server.js';
export default {
	components: {
		uniLoadMore,
	},
	data() {
		return {
			TabCur:0,
			postList:[],
			status: 'loading',
			type:"new",
			classinfo:[],
			statusTypes: [{
				value: 'more',
				text: '加载前',
				checked: true
			}, {
				value: 'loading',
				text: '加载中',
				checked: false
			}, {
				value: 'noMore',
				text: '我是有底线的',
				checked: false
			}],
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '我是有底线的'
			},
		}
	},
	onLoad:function(e){
		this.classId=e.id;
		this.getClassInfo(this.classId);
		this.getPostList(this.classId);
	},
	methods: {
		getPushPost:function(){
			if(server.userinfo.Auid==""||server.userinfo.Auid==null){
				uni.showToast({
					title: "你还没有登录，请登录后再来吧",
					position:'bottom',
					icon:'none'
				});
				setTimeout(function () {
					uni.navigateTo({
						url: '../reg/reg'
					})
				}, 1200);
			}else{
				server.postClass=this.classinfo;
				uni.navigateTo({
					url: '../post/post'
				})
			}
		},
		getClassInfo:function(e){
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/post.php?type=getClassInfo', //仅为示例，并非真实接口地址。
				data: {
					classId:e,
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					uni.hideLoading();
					this.classinfo=[]
					this.classinfo=res.data.data;
					console.log(this.classinfo);
					this.weikong=false
					if(res.data.code!=="1"){
						uni.showToast({
							title: '获取话题信息失败',
							position:'bottom',
							icon:'none'
						});
					}
					this.$forceUpdate();
				}
			});
		},
		getpostinfo:function(id){
			uni.navigateTo({
				url: '../postinfo/postinfo?id='+id
			});
		},
		getbieren:function(e){
			if(e==server.userinfo.Auid){
				uni.navigateTo({
					url: '../i/i'
				})
			}else{
				uni.navigateTo({
					url: '../i/bieren?auid='+e
				})
			}
		},
		getPostList:function(classId){
			uni.showLoading({
				title: '获取数据中'
			});
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/post.php?type=getClassPostList', //仅为示例，并非真实接口地址。
				data: {
					page: 1,
					count:20,
					classId:classId,
					postType:this.type
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					uni.hideLoading();
					this.postList=[]
					console.log("————————————帖子列表——————————");
					this.postList=res.data.data;
					console.log(this.postList);
					this.weikong=false
					if(res.data.code!=="1"){
						uni.showToast({
							title: '获取帖子失败，建议重启',
							position:'bottom',
							icon:'none'
						});
					}
					if(res.data.data==false){
						this.weikong=true
					}
					//this.$forceUpdate();
				},
				complete:function(){
					uni.hideLoading();
				},
				
			});
		},
		showImage:function(res,c){
			uni.previewImage({
				current:c,
				urls: res,
				longPressActions: {
				itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function (res) {
						
						uni.showToast({
							title: '选中了第' + (res.tapIndex + 1) + '个按钮',
							position:'bottom',
							icon:'none'
						});
						console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
						
					},
					fail: function (res) {
						console.log(res.errMsg);
					}    
				}
			})
		},
		caidan:function(res){
			console.log(res)
			this.Dindex=res;
			if(res.AuthorId==server.userinfo.Auid){
				this.menuList=['分享给朋友', '生成海报', '举报','删除帖子']
			}else{
				this.menuList=['分享给朋友', '生成海报', '举报']
			}
			uni.showActionSheet({
				itemList:this.menuList,
				success: function (res) {
					switch(res.tapIndex){
						case 0:
							uni.showToast({
								title: "分享"+res.Content,
								position:'bottom',
								icon:'none'
							});
							break;
						case 1:
							
							break;
						case 2:
							
							break;
						case 3:
						
							break;
						default:
							
					}
				},
				fail: function (res) {
					console.log(res.errMsg);
				}
			});
		},
		showImage(res,c){
			uni.previewImage({
				current:c,
				urls: res,
				longPressActions: {
				itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function (res) {
						
						uni.showToast({
							title: '选中了第' + (res.tapIndex + 1) + '个按钮',
							position:'bottom',
							icon:'none'
						});
						console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
						
					},
					fail: function (res) {
						console.log(res.errMsg);
					}    
				}
			})
		},
		Like:function(postid,auid,give,index){
			if(give===true){
				var modea='del'
			}else{
				var modea='add'
			}
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/post.php?type=Like", //仅为示例，并非真实接口地址。
				data: {
					fuid: auid,
					postid:postid,
					mode:modea
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					if(res.data.code=="1"){
						if(modea=='add'){
							this.postList[index].Give=true;
							uni.showToast({
								title: "种草成功！",
								position:'bottom',
								icon:'none'
							});
						}else{
							this.postList[index].Give=false;
							uni.showToast({
								title: "取消种草成功！",
								position:'bottom',
								icon:'none'
							});
						}
						
						this.$forceUpdate()
					}else{
						
						uni.showToast({
							title: "种草失败！",
							position:'bottom',
							icon:'none'
						});
					}
				},
				complete() {
					
				}
			});
		},
		tabSelect(e) {
			this.TabCur = e
			if(e==0){
				this.type="new";
			}else{
				this.type="hot";
			}
			
		}
	}
}
